        export default {
		data() {
			return {
                id:'',
                shopInfo: ''
			}
		},
		onLoad(options) {
            this.id = options?options.id:''
            this._ShopDetail({id:this.id})
		},
		methods: {
            async _ShopDetail(data){
                let res = await this.$apis.ShopDetail(data)
                this.shopInfo = res.result.shopInfo
            }
        }
	}
